<template>
  <div class="home-page">
    <!-- Hero Section -->
    <div class="hero-section">
      <div class="hero-overlay"></div>
      <div class="container py-5 position-relative" style="z-index: 2;">
        <div class="row align-items-center min-vh-70">
          <div class="col-lg-6 text-white mb-4 mb-lg-0">
            <div class="hero-content" data-aos="fade-right">
              <h1 class="display-3 fw-bold mb-4 text-white">
                {{ settings.hero?.hero_title || 'Selamat Datang di Rumah Sakit Kami' }}
              </h1>
              <p class="lead mb-4 text-white" style="font-size: 1.25rem; line-height: 1.8;">
                {{ settings.hero?.hero_description || 'Kami berkomitmen untuk memberikan pelayanan kesehatan terbaik dengan teknologi modern dan tim profesional yang berpengalaman.' }}
              </p>
              <div class="hero-buttons">
                <router-link 
                  :to="settings.hero?.hero_button_1_link || '/services'" 
                  class="btn btn-light btn-lg me-3 mb-2 px-4 py-3 shadow-lg"
                  style="border-radius: 50px; font-weight: 600;">
                  {{ settings.hero?.hero_button_1_text || 'Lihat Layanan' }}
                </router-link>
                <router-link 
                  :to="settings.hero?.hero_button_2_link || '/doctors'" 
                  class="btn btn-outline-light btn-lg mb-2 px-4 py-3"
                  style="border-radius: 50px; font-weight: 600; border-width: 2px;">
                  {{ settings.hero?.hero_button_2_text || 'Tim Dokter' }}
                </router-link>
              </div>
            </div>
          </div>
          <div class="col-lg-6" data-aos="fade-left">
            <div class="hero-image-container">
              <img 
                :src="settings.hero?.hero_image || 'https://images.unsplash.com/photo-1519494026892-80bbd2d6fd0d?w=600&h=400&fit=crop'" 
                alt="Rumah Sakit" 
                class="hero-image"
                @error="handleImageError"
              >
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Stats Section -->
    <div class="stats-section py-5 bg-light">
      <div class="container">
        <div class="row text-center">
          <div class="col-md-3">
            <div class="stat-card">
              <h3 class="h2 text-primary">10+</h3>
              <p class="text-muted">Dokter Profesional</p>
            </div>
          </div>
          <div class="col-md-3">
            <div class="stat-card">
              <h3 class="h2 text-success">500+</h3>
              <p class="text-muted">Pasien Puas</p>
            </div>
          </div>
          <div class="col-md-3">
            <div class="stat-card">
              <h3 class="h2 text-info">10+</h3>
              <p class="text-muted">Layanan Medis</p>
            </div>
          </div>
          <div class="col-md-3">
            <div class="stat-card">
              <h3 class="h2 text-warning">24/7</h3>
              <p class="text-muted">Layanan Darurat</p>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Services Preview -->
    <div class="services-preview py-5">
      <div class="container">
        <h2 class="text-center mb-5">Layanan Unggulan Kami</h2>
        <div class="row">
          <div class="col-md-4 mb-4" v-for="service in services.slice(0, 3)" :key="service.id">
            <div class="card h-100 shadow-sm">
              <div class="card-body">
                <h5 class="card-title">{{ service.name }}</h5>
                <p class="card-text">{{ service.description }}</p>
                <router-link to="/services" class="btn btn-primary btn-sm">
                  Selengkapnya
                </router-link>
              </div>
            </div>
          </div>
        </div>
        <div class="text-center mt-4">
          <router-link to="/services" class="btn btn-primary btn-lg">
            Lihat Semua Layanan
          </router-link>
        </div>
      </div>
    </div>

    <!-- Doctors Section -->
    <div class="doctors-section py-5">
      <div class="container">
        <h2 class="text-center mb-5">Tim Dokter Kami</h2>
        <div class="row">
          <div class="col-md-3 mb-4" v-for="doctor in doctors.slice(0, 4)" :key="doctor.id">
            <div class="card text-center shadow-sm h-100">
              <img :src="doctor.photo || 'https://via.placeholder.com/150?text=Doctor'" 
                   class="card-img-top doctor-photo" 
                   :alt="doctor.name">
              <div class="card-body">
                <h5 class="card-title">{{ doctor.name }}</h5>
                <p class="text-primary mb-2"><strong>{{ doctor.specialization }}</strong></p>
                <p class="card-text small text-muted">{{ doctor.bio ? doctor.bio.substring(0, 80) + '...' : '' }}</p>
              </div>
            </div>
          </div>
        </div>
        <div class="text-center mt-4">
          <router-link to="/doctors" class="btn btn-primary btn-lg">
            Lihat Semua Dokter
          </router-link>
        </div>
      </div>
    </div>

    <!-- Testimonials Section -->
    <div class="testimonials-section py-5 bg-light">
      <div class="container">
        <h2 class="text-center mb-5">Testimoni Pasien</h2>
        <div class="testimonials-marquee">
          <div class="marquee-content">
            <div class="testimonial-card" v-for="testimonial in testimonials" :key="testimonial.id">
              <div class="card shadow-sm">
                <div class="card-body">
                  <h6 class="card-title mb-2">{{ testimonial.patient_name }}</h6>
                  <div class="stars mb-2">
                    <span v-for="n in 5" :key="n" :class="n <= testimonial.rating ? 'text-warning' : 'text-muted'"></span>
                  </div>
                  <p class="card-text">"{{ testimonial.message }}"</p>
                  <small class="text-muted">{{ formatDate(testimonial.created_at) }}</small>
                </div>
              </div>
            </div>
            <!-- Duplicate for seamless loop -->
            <div class="testimonial-card" v-for="testimonial in testimonials" :key="'dup-' + testimonial.id">
              <div class="card shadow-sm">
                <div class="card-body">
                  <h6 class="card-title mb-2">{{ testimonial.patient_name }}</h6>
                  <div class="stars mb-2">
                    <span v-for="n in 5" :key="n" :class="n <= testimonial.rating ? 'text-warning' : 'text-muted'"></span>
                  </div>
                  <p class="card-text">"{{ testimonial.message }}"</p>
                  <small class="text-muted">{{ formatDate(testimonial.created_at) }}</small>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Partners Section -->
    <div class="partners-section py-5">
      <div class="container">
        <h2 class="text-center mb-5">Mitra Kami</h2>
        <div class="partners-marquee">
          <div class="marquee-content-partners">
            <div class="partner-logo" v-for="partner in partners" :key="partner.id">
              <img :src="partner.logo" :alt="partner.name" />
            </div>
            <!-- Duplicate for seamless loop -->
            <div class="partner-logo" v-for="partner in partners" :key="'dup-' + partner.id">
              <img :src="partner.logo" :alt="partner.name" />
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Recent News -->
    <div class="recent-news py-5">
      <div class="container">
        <h2 class="text-center mb-5">Berita Terbaru</h2>
        <div class="row">
          <div class="col-md-4 mb-4" v-for="item in news.slice(0, 3)" :key="item.id">
            <div class="card shadow-sm">
              <img v-if="item.featured_image" :src="item.featured_image" class="card-img-top" alt="News">
              <div class="card-body">
                <h5 class="card-title">{{ item.title }}</h5>
                <p class="card-text">{{ item.excerpt }}</p>
                <small class="text-muted">{{ formatDate(item.published_at) }}</small><br>
                <router-link :to="`/news/${item.id}`" class="btn btn-primary btn-sm mt-2">
                  Baca Selengkapnya
                </router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomePage',
  data() {
    return {
      services: [],
      news: [],
      doctors: [],
      testimonials: [],
      partners: [],
      settings: {}
    }
  },
  methods: {
    handleImageError(event) {
      // Fallback ke gambar default jika error
      event.target.src = 'https://images.unsplash.com/photo-1519494026892-80bbd2d6fd0d?w=600&h=400&fit=crop';
    },
    formatDate(date) {
      return new Date(date).toLocaleDateString('id-ID', {
        year: 'numeric',
        month: 'long',
        day: 'numeric'
      });
    },
    async fetchServices() {
      try {
        const response = await this.$axios.get('/api/v2/services');
        this.services = response.data.data;
      } catch (error) {
        console.error('Error fetching services:', error);
      }
    },
    async fetchNews() {
      try {
        const response = await this.$axios.get('/api/v2/news');
        this.news = response.data.data;
      } catch (error) {
        console.error('Error fetching news:', error);
      }
    },
    async fetchDoctors() {
      try {
        const response = await this.$axios.get('/api/v2/doctors');
        this.doctors = response.data.data;
      } catch (error) {
        console.error('Error fetching doctors:', error);
      }
    },
    async fetchTestimonials() {
      try {
        const response = await this.$axios.get('/api/v2/testimonials');
        this.testimonials = response.data.data;
      } catch (error) {
        console.error('Error fetching testimonials:', error);
      }
    },
    async fetchPartners() {
      try {
        const response = await this.$axios.get('/api/v2/partners');
        this.partners = response.data.data;
      } catch (error) {
        console.error('Error fetching partners:', error);
      }
    },
    async fetchSettings() {
      try {
        const response = await this.$axios.get('/api/v2/settings');
        this.settings = response.data.data;
      } catch (error) {
        console.error('Error fetching settings:', error);
      }
    }
  },
  mounted() {
    this.fetchSettings();
    this.fetchServices();
    this.fetchNews();
    this.fetchDoctors();
    this.fetchTestimonials();
    this.fetchPartners();
  }
}
</script>

<style scoped>
.hero-section {
  background: linear-gradient(135deg, #0d7377 0%, #0dd1c2 100%);
}

.stat-card {
  padding: 20px;
}

.card {
  border: none;
  transition: transform 0.3s ease;
}

.card:hover {
  transform: translateY(-5px);
}

.doctor-photo {
  height: 200px;
  object-fit: cover;
}

.stars {
  font-size: 1.2rem;
}

/* Testimonials Marquee */
.testimonials-marquee {
  overflow: hidden;
  position: relative;
  width: 100%;
}

.marquee-content {
  display: flex;
  animation: scroll-testimonials 40s linear infinite;
}

.testimonial-card {
  flex: 0 0 350px;
  margin: 0 15px;
}

.testimonial-card .card {
  height: 100%;
  min-height: 200px;
}

@keyframes scroll-testimonials {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}

.marquee-content:hover {
  animation-play-state: paused;
}

/* Partners Marquee */
.partners-marquee {
  overflow: hidden;
  position: relative;
  width: 100%;
}

.marquee-content-partners {
  display: flex;
  align-items: center;
  animation: scroll-partners 30s linear infinite;
}

.partner-logo {
  flex: 0 0 200px;
  margin: 0 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.partner-logo img {
  max-width: 160px;
  max-height: 80px;
  object-fit: contain;
  filter: grayscale(100%);
  opacity: 0.7;
  transition: all 0.3s ease;
}

.partner-logo img:hover {
  filter: grayscale(0%);
  opacity: 1;
}

@keyframes scroll-partners {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}

.marquee-content-partners:hover {
  animation-play-state: paused;
}

/* Hero Section Enhanced */
.hero-section {
  position: relative;
  background: linear-gradient(135deg, #0d7377 0%, #14FFEC 100%);
  min-height: 100vh;
  display: flex;
  align-items: center;
  overflow: hidden;
}

.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(13, 115, 119, 0.95) 0%, rgba(13, 209, 194, 0.85) 100%);
  z-index: 1;
}

.min-vh-70 {
  min-height: 70vh;
}

.hero-content {
  animation: fadeInUp 1s ease-out;
}

.hero-image-container {
  position: relative;
  animation: fadeInRight 1s ease-out;
}

.hero-image {
  width: 100%;
  height: auto;
  max-height: 500px;
  object-fit: cover;
  border-radius: 20px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  border: 5px solid rgba(255, 255, 255, 0.2);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.hero-image:hover {
  transform: translateY(-10px) scale(1.02);
  box-shadow: 0 30px 80px rgba(0, 0, 0, 0.4);
}

.hero-buttons .btn {
  transition: all 0.3s ease;
}

.hero-buttons .btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Responsive */
@media (max-width: 991px) {
  .hero-section {
    min-height: auto;
    padding: 60px 0;
  }
  
  .hero-image {
    max-height: 350px;
  }
  
  .display-3 {
    font-size: 2.5rem;
  }
}
</style>
